@import "_base/fonts";
@import "_base/grid";
@import "_base/reset";
html {
  font-size: 10px; }
// //////////////////////////////////////
//            BLOCKS
// //////////////////////////////////////
// //////////////////////////////////////
//            NAV
// //////////////////////////////////////
.navBar {
  background-color: rgba(255, 255, 255, 0.7);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  text-align: center;
  z-index: 10;
  // border: 1px solid black
  &__logo {
    color: #626262;
    font-size: 3.8rem;
    text-transform: uppercase;
    width: 160px;
    font-family: HelveticaBold;
    margin: 27px 0 23px 18px;
    line-hieght: 0;
    span {
      line-hieght: 0;
      color: #56c2b9; } }
  &__menu {
    font-size: 1.8rem;
    width: 593px;
    font-family: Helvetica;
    &__list {
      display: flex;
      justify-content: flex-end;
      &--item {
        margin-top: 40px;
        &:hover .subMenu {
          display: flex; }

        &--link {
          padding: 40px 13px 29px 13px;
          color: #626262;
          &:hover {
            border-bottom: 1px solid #d94855;
            background-color: #cf1a2b;
            color: #fff; } } } } } }
.navBar__cart {
  width: 253px;
  margin-top: 32px;
  margin-right: 18px;
  &--item {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    position: relative; } }
.cart__sum {
  padding: 4px 15px 0 12px;
  color: #b1b1b1;
  font-size: 2.4rem; }
.down {
  margin-top: 8px; }
// //////////////////////////////////////
//          SUBMENU
// //////////////////////////////////////
.subMenu {
  width: 605px;
  display: flex;
  flex-direction: row;
  background-color: #cf1a2b;
  position: absolute;
  top: 88px;
  &__logo {
    color: #fff;
    font-size: 3.8rem;
    text-transform: uppercase;
    &--text {
      width: 183px;
      font-family: HelveticaBold;
      padding: 20px 0 12px 35px;
      border-bottom: 1px solid #d94855;
      span {
        color: #fec8cd; } }
    &--img {
      padding: 50px 0 50px 72px; } }
  &__list {
    width: 193px;
    border-left: 1px solid #d94855;
    &--item {
      font-size: 1.6rem;
      margin-top: 24px;
      margin-left: 36px;
      color: #f2bfc4; }
    &--item:last-child {
      margin-bottom: 25px; } } }
.isHide {
  display: none; }
