////////////////////////////////////////
//          LEVIS
////////////////////////////////////////
.levis {
  width: 100%;
  background-color: #fff;
  &__info {
    width: 100%;
    position: relative;
    display: flex;
    flex-direction: row;
    &::after {
      position: absolute;
      background: url('../img/levis/levis.jpg') no-repeat;
      width: 380px;
      height: 175px;
      top: -16px;
      left: 14px;
      z-index: 1;
      content: ''; }
    &--l {
      position: absolute;
      top: -16px;
      left: 360px; }
    &--r {
      position: absolute;
      top: -16px; }
    &__text {
      width: 520px;
      height: 500px;
      padding: 200px 10px 0 50px;
      box-sizing: border-box;
      p {
        font-size: 5rem;
        font-family: HelveticaBold;
        text-transform: capitalize; }
      &--l {
        font-size: 1.4rem;
        font-family: HelveticaUltraLight; }
      &__price {
        font-size: 3.4rem;
        font-family: HelveticaRegular;
        padding-top: 50px;
        a {
          font-size: 2.5rem;
          background-color: #cf1a2b;
          color: #fff;
          padding: 10px 5px;
          margin-left: 90px; }
        span {
          font-family: HelveticaBold; } } } }
  &__items {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    margin: 50px 0;
    &--1 {
      border: 1px solid black;
      &:hover {
        border: 1px solid grey; } } } }
